.format-section {
  margin-bottom: 80px;
  &__slider {
    display: flex;
    gap: 32px;
    align-items: center;
  }

  &__conteiner {
    overflow: hidden;
  }

  &__list {
    margin-bottom: 32px;
  }

  &__button {
    display: none;

    @media screen and (min-width: 768px) {
      display: block;
    }
  }
}
