.format-card {
  transition: 0.3s;

  &:hover {
    transform: scale(1.05);
  }

  &__conteiner {
    display: flex;
    flex-direction: row;
    justify-content: end;
    position: relative;
    height: 444px;
    border: 1px solid lightgray;
    border-radius: 16px;
    padding: 24px;
    background-image: linear-gradient(
      180deg,
      rgba(49, 38, 26, 0) 55.21%,
      rgba(49, 38, 26, 0.71) 72.92%,
      var(--color-primary-dark) 100%
    );

    @media screen and (min-width: 768px) {
      flex-direction: column;
    }
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 16px;
    z-index: -1;
  }
  &__title {
    margin-bottom: 80px;
    font-size: 24px;
    font-weight: 600;
    color: var(--color-primary-light);
  }
  &__text {
    font-size: 14px;
    font-weight: 300;
    color: var(--color-primary-light);
  }
}
