.traditions-section {
  padding-top: 40px;

  @media screen and (min-width: 768px) {
    padding: 96px;

    &__container {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      gap: 40px;
    }
  }

  &__title {
    margin-bottom: 42px;
  }

  &__content {
    max-width: 710px;
  }

  &__text {
    &--limited {
      max-width: 332px;
    }
  }
  &__image {
    display: none;

    @media screen and (min-width: 768px) {
      display: block;
      border-radius: 200px 200px 4px 4px;
      width: 606px;
      height: 466px;
    }
  }
}
